.skills-section {
  padding: 4rem 2rem;
  background: #0f172a;
  color: #ffffff;
  text-align: center;
}

.skills-section h2 {
  font-size: 2.8rem;
  margin-bottom: 3rem;
  color: #64ffda;
  text-shadow: 0 2px 10px rgba(100, 255, 218, 0.2);
}

.skill-group {
  margin-bottom: 4rem;
}

.skill-group h3 {
  font-size: 1.5rem;
  color: #8892b0;
  margin-bottom: 1rem;
}

.skill-items {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  gap: 1rem;
}

.skill-item {
  background: rgba(255, 255, 255, 0.05);
  border: 1px solid rgba(100, 255, 218, 0.1);
  padding: 0.75rem 1.5rem;
  border-radius: 8px;
  font-weight: 600;
  font-size: 1rem;
  color: #ccd6f6;
  backdrop-filter: blur(6px);
  box-shadow: 0 4px 20px rgba(0, 0, 0, 0.1);
  transition: all 0.3s ease;
  cursor: default;
  transform-style: preserve-3d;
  will-change: transform;
}

.skill-item:hover {
  color: #0f172a;
  background-color: #64ffda;
  transform: scale(1.08) rotateX(4deg) rotateY(4deg);
  box-shadow: 0 8px 24px rgba(100, 255, 218, 0.25);
}
