/* src/pages/NotFound.css */
.notfound-container {
    height: 100vh;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    background: #0a192f;
    color: #ccd6f6;
    text-align: center;
    padding: 20px;
}
  
.notfound-container h1 {
    font-size: 8rem;
    color: #64ffda;
    margin-bottom: 20px;
}
  
.notfound-container p {
    font-size: 1.5rem;
    margin-bottom: 30px;
}
  
.back-home {
    padding: 10px 20px;
    background: #64ffda;
    color: #0a192f;
    text-decoration: none;
    font-weight: bold;
    border-radius: 5px;
    transition: all 0.3s ease;
}
  
.back-home:hover {
    background: #52d1b4;
}
  