/* src/components/Footer.css */
.footer {
    padding: 20px 0;
    text-align: center;
    font-size: 0.9rem;
    color: #8892b0;
    background-color: #0a192f;
    border-top: 1px solid #233554;
}
  
.footer-links {
    display: flex;
    justify-content: center;
    gap: 20px;
    margin-bottom: 10px;
}
  
.contact-icon {
    font-size: 24px;
    color: #8892b0;
    transition: transform 0.3s ease, color 0.3s ease;
}
  
.contact-icon:hover {
    transform: scale(1.2);
    color: #00bfff;
}
  